


































































	var myTimeInterval = '';
	export default {
		data() {
			return {
				id:'',//用户id
				title: 'video',
				src: '',
// 				danmuList: [{
// 						text: '第 1s 出现的弹幕',
// 						color: '#ff0000',
// 						time: 1
// 					},
// 					{
// 						text: '第 3s 出现的弹幕',
// 						color: '#ff00ff',
// 						time: 3
// 					}
// 				],
				// danmuValue: '',
				banner:{},
				type:2,//video
				defaultImgUrl:"../../../../static/img/headImage.png",
				commentList:[],
				pageNum:5,//评论数
				pageStart:0,//评论页数
				text:'',//评论内容
				haveMoreComment:true
			}
		},
		onLoad(event) {
			let that = this;
			 // 目前在某些平台参数会被主动 decode，暂时这样处理。
			try {
			    this.banner = JSON.parse(decodeURIComponent(event.detailDate));
			} catch (error) {
			    this.banner = JSON.parse(event.detailDate);
			}
			this.banner.content =this.banner.content.replace(/\\/g, "").replace(/<img/g,
			"<img style=\"width:100%;\"");
			// console.log(this.banner)
			 this.getDetail();
			uni.setNavigationBarTitle({
			    title: this.banner.name
			});
			this.getCommentList();
			myTimeInterval = setInterval(function(){
				if(this.id != ''){
					uni.request({
					    url: that.websiteUrl+'uniApp/score/addUserScore?userId=' + that.id+'&type=3'
					})
					console.log('两分钟+2分');
				}
				console.log('定时器')
			},120000)//每两分钟执行一次，
		},
		onReady: function(res) {

			this.videoContext = uni.createVideoContext('myVideo')

		},
		onUnload (){
				  console.log('页面关闭')
				  clearInterval(myTimeInterval);
		},
		methods: {
			sendDanmu: function() {
				this.videoContext.sendDanmu({
					text: this.danmuValue,
					color: this.getRandomColor()
				});
				this.danmuValue = '';
			},
			videoErrorCallback: function(e) {
				uni.showModal({
					content: e.target.errMsg,
					showCancel: false
				})
			},
			getRandomColor: function() {
				const rgb = []
				for (let i = 0; i < 3; ++i) {
					let color = Math.floor(Math.random() * 256).toString(16)
					color = color.length == 1 ? '0' + color : color
					rgb.push(color)
				}
				return '#' + rgb.join('')
			},
			getDetail : function(){
				//发送加分请求
				let that = this;
				this.id = uni.getStorageSync('id');
				console.log("Id = " + this.id)
				if(this.id != ''){
					uni.request({
						url: this.websiteUrl+'uniApp/score/addUserScore',
						data:{
							 userId:that.id,
							 type:5
						},
						success:(result)=>{
							if(result.data.data.display != 0){
								uni.showToast({
									icon:'none',
									title: result.data.data.message,
									duration: 2000
								});
							}
						}
					})
				}
			},
			getCommentList(){
				let that = this;
				let data = {
					type:that.type,
					targetId:that.banner.id,
					pageNum:that.pageNum,
					pageStart:that.pageStart
				};
				uni.request({
					url: this.websiteUrl+'uniApp/comment/getCommentListByIdAndType',
					data: data,
					success: (result) => {
						that.commentList = result.data.data.list;
					},
					fail: (data, code) => {
						uni.stopPullDownRefresh();
						console.log('fail' + JSON.stringify(data));
					}
				})
			},
			loadMoreComment(){
				this.haveMoreComment = false;
			},
			checklogin(){
				this.id = uni.getStorageSync('id');
				if(this.id == null || this.id == ''){
					return false;
				}else{
					return true;
				}
			},
			send(e) {
				if (!this.checklogin()) {
					uni.showModal({
						content: '请登录后再操作',
						confirmText:'马上登录',
						success: function (res) {
							if (res.confirm) {
								uni.navigateTo({
								    url: '../../login/login',
								});
							} else if (res.cancel) {
								
							}
						}
					});
					return false;
				}
				if (!this.text) {
					uni.showToast({
						title: '请输入评论内容',
						icon: 'none'
					});
					return false;
				}
				var self = this;
				
				var data = {
					userId:self.id,
					type:self.type,
					targetId:self.banner.id,
					parentId:0,
					rootId:0,
					parentUserId:0,
					commentContent:self.text
				};
				
				uni.request({
					url:this.websiteUrl+'uniApp/comment/addComment',
					data: data,
					success: (result) => {
						//评论成功，才重新请求comment列表
						if(result.data.status == 1){
							self.getCommentList();
						}else{
							uni.showToast({
								 title:  result.data.message,
								duration: 2000
							});
						}
					}
				})
			},
			goToCommentDetail(value){
				uni.navigateTo({
					url: "../comment/commentDetail?id=" + value.id+"&type=2"
				})
			}
		}
	}
